import React from "react"
import { Box, Fade, Typography } from "@rp/react/web/mui/core"

type PDFViewHeaderPagesProps = {
  currentPage: number | string
  numPages: number
  color?: string
}

function PDFViewHeaderPages({ currentPage, numPages, color }: PDFViewHeaderPagesProps) {
  if (!currentPage || !numPages) return null

  return (
    <Box>
      {currentPage && numPages ? (
        <Fade in>
          <Typography fontSize={16} color={color}>
            Page {currentPage} of {numPages}
          </Typography>
        </Fade>
      ) : null}
    </Box>
  )
}

export { PDFViewHeaderPages }
